.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.box, .el {
  height: 42px;
  width: 42px;
  border-radius: 15%;
  opacity: 0.75;
}
.title-animation, base {
    width: auto;
    height: 100vh;
    background-color: #000;
}

.title-animation-title {
    color: #00aaff;
    margin: auto;
    font-size: 4vh;
    padding-left: 10%;
}

.title-animation-side, .scroll-indicator {
    color: #00aaff;
    margin: auto;
    padding-left: 10%;
}

.title-animation-side {
    font-size: 2.5vh;
    color: #fff;
    margin-top: 4vh;
    margin-right: '2vh';
    opacity: '0.9';
}

.unleashed-ioit {
    font-size: 2.5vh;
}

.title-animation-side-head {
    color: #00aaff;
    margin: auto;
    padding-left: 10%;
    margin-top: 25vh;
    font-size: 3.5vh;
}


.scroll-indicator {
    margin-top: 20vh;
    font-size: 4vh;
    color: #00aaff;
    padding-right: 5.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

@media screen and (max-width: 786px) {
    .title-animation-side-head {
        text-align: left;
        margin: 0px 0px;
        font-size: 2.2vh;
        padding: 0% 5%;
    }

    .title-animation-title {
        font-size: 2.8vh;
        padding-left: 5%;
        padding-top: 5vh;
    }

    .unleashed, .unleashed-ioit {
        font-size: 2vh;
    }

    .title-animation-side {
        font-size: 2vh;
        padding: 0% 5%;
        text-align: justify;
    }

    .scroll-indicator {
        padding-top: unset;
        margin: 10vh 0px 0px 0px;
        padding: 0px 47%;
    }
}
.animation-wrapper {
    width: 50%;
    padding-bottom: 50%;
}

.sphere-animation {
    width: 50vh;
    height: 50vh;
    margin-left: 27%;
    margin-top: 5%;
}

.sphere path {
    fill: url(#sphereGradient);
    stroke-width: 1px;
    stroke: rgba(53, 53, 53, 0.35);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

@media (min-width: 500px) {
    .sphere path {
        stroke-width: 2px;
    }
}

@media screen and (max-width: 786px) {
    .sphere-animation {
        margin-left: 2vw;
        display: none;
    }

    .animation-wrapper {
        display: none;
    }
}
.contact-div {
    color : '#000';
