#contact-head{
    
    font-size: .8em;
    font-weight: 900;
    margin:1em
}

.contact-form{
    position: relative;
    padding:10px;
    width: 100%;
    box-sizing: border-box;
}


.txt-field{
    position: relative;
    margin: 0;
    border-bottom: 1px solid  #080808;
}

.txt-field input{
    border : none;
    background: none;
    outline:none;
    width: 100%;
    min-height: 5vh;
}
.txt-field label{
    position: absolute;
    left:1%;
    pointer-events: none;
    transition: .5s;
   
    top:-15px;
}

.txt-field input:focus ~ label{
    
    color: var(--high-color);
}

.txt-field span::before{
    content: '';
    position: absolute;
    background-color: var(--high-color);
    width: 0%;
    height: 2px;
    left:0;
    top: 100%;
    transition: .5s;
}

.txt-field input:focus ~ span::before{
    width: 100%;
}

.txt-field + .txt-field{
    /* border:solid 2px var(--high-color); */
    margin-top:2em;
}

.contact-form button{
    position: relative;
    outline: none;
    padding:.4em;
    width:40%;
    height: 12%;
    margin-top: 2%;
    border:none;
    color:#fff;
    background-color: #000;
    border-radius: 10px 0 0 10px;
    /* border : 2px solid var(--high-color); */
    right:calc(-60% - 10px);
}

.cta svg{
    display: none;
}


