.sections{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 5rem;
    gap: 5rem;
    scroll-snap-type: y mandatory;
}

.sections .section{
    width: 100%;
    min-height: calc(100%/3);
    display: flex;
    align-items: center;
    background: #333;
    color: #fff;
    box-shadow: 0px 0px 5px rgb(255, 255, 255);
    border-radius: 10px;
    scroll-snap-align: start;
    transition: all .3s;
}



.image{
    width: 30%;
