    /* flex-direction: column; */
}
.content{
    width: 70%;
    height: 100%;
    font-size: 2.5vh;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.sections .section2{
    flex-direction: row-reverse;
}

.sections .section2 .content{
    align-items: end;
}

.text-black{
    color: #000 !important;
}
