.contact-body {
   
    height:100vh;
    width:100vw;
    min-height: 100vh;
    --main-bg-color:#a9e7da ;
    --secondary-color:#A9E7DA;
    --high-color :#00aaff;
    /* background-color: #000; */
    /* padding: 10vh 6vw; */
    background-color: #000;
    font-size:2.5vh;
    padding: 10vh 6vw 1vh 6vw;
    color: #fff;
    font-weight: 500;
    font-family: "Fira Sans", sans-serif;
    
    
    display: flex;
    flex-direction: column;
    align-items: center;
    /* border:solid 2px var(--high-color); */
}

.contact-card{
    position: relative;
    background-color: #fff;
    height: 60%;
    width: 80%;
    /* margin:2vw; */
    border-radius: 6px;
    color:#000;
    transition: 1s;
    font-size: 80%;

}

#contact-head{
    
    font-size: .8em;
    font-weight: 900;
    margin:1em
}

.contact-form{
    position: relative;
    padding:10px;
    width: 100%;
    box-sizing: border-box;
}


.txt-field{
    position: relative;
    margin: 0;
    border-bottom: 1px solid  #080808;
}

.txt-field input{
    border : none;
    background: none;
    outline:none;
    width: 100%;
    min-height: 5vh;
}
.txt-field label{
    position: absolute;
    left:1%;
    pointer-events: none;
    transition: .5s;
   
    top:-15px;
}

.txt-field input:focus ~ label{
    
    color: var(--high-color);
}

.txt-field span::before{
    content: '';
    position: absolute;
    background-color: var(--high-color);
    width: 0%;
    height: 2px;
    left:0;
    top: 100%;
    transition: .5s;
}

.txt-field input:focus ~ span::before{
    width: 100%;
}

.txt-field + .txt-field{
    /* border:solid 2px var(--high-color); */
    margin-top:2em;
}

.contact-form button{
    position: relative;
    outline: none;
    padding:.4em;
    width:40%;
    height: 12%;
    margin-top: 2%;
    border:none;
    color:#fff;
    background-color: #000;
    border-radius: 10px 0 0 10px;
    /* border : 2px solid var(--high-color); */
    right:calc(-60% - 10px);
}

.cta svg{
    display: none;
}


/* map */

.map-box{
    display: none;
    
}

.contact-us{

    font-family: 'Anton', sans-serif;
    font-weight: bolder;
    bottom: 0;
    color:#fff;
    font-size:5vw;
    height:6%;
    margin-bottom: 2px;
    /* border : 2px solid var(--high-color); */
    z-index: 1000;
    width:80%;
    /* text-align: center; */
    
}

.contact-footer {
    display: none;
    /* border : 2px solid var(--high-color); */
    /* position: relative;
    bottom:1%;
    padding:0 4%;
    width:80%;
    display: flex;
    flex-direction: column; */
}



.bottom-info {
    display: flex;
    justify-content: space-between;
}

.bottom-info-card div{
    display: none;
}

.bottom-info-card {
    font-size: 2vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.social{
    display: none;
}

.container-svg{
    /* border : 2px solid var(--secondary-color); */
    height: 30%;
}



/*mediaqueries */

@media screen and (max-width: 310px){
    .cta svg {
       display: none;
       }
}
